import AppSlider from '../AppSlider/AppSlider';

export default class AppView {
  constructor(data) {
    this.data = data;
  }

  static addMediaQuery() {
    const metaResponce = document.createElement('meta');
    metaResponce.name = 'viewport';
    metaResponce.content = 'width=device-width, initial-scale=1, user-scalable=no';
    document.head.appendChild(metaResponce);
  }

  static embedFontAwesome() {
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    link.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
    link.setAttribute('crossorigin', 'anonymous');
    document.head.appendChild(link);
  }

  static cutLongString(str, term) {
    let shortStr = str;
    if (str.length > term) {
      shortStr = str.slice(0, term);
      shortStr = `${shortStr}...`;
    }
    return shortStr;
  }

  render() {
    const content = document.createElement('section');
    content.className = 'slider';

    document.body.appendChild(content);

    AppView.addClips(this.data);

    AppView.addMediaQuery();

    AppView.embedFontAwesome();

    const appSlider = new AppSlider(this.data);
    appSlider.addSliderView();
  }

  static addClips(data) {
    data.forEach((element) => {
      const videoCard = document.createElement('div');
      const titleP = document.createElement('p');
      const titleDiv = document.createElement('div');
      titleDiv.className = 'title';
      titleDiv.appendChild(titleP);
      titleP.innerHTML = `<a href=${element.videoID} target="_blank">${AppView.cutLongString(element.title, 45)}</a>`;
      const descriptionP = document.createElement('p');
      descriptionP.className = 'description';
      descriptionP.innerHTML = element.description;
      const channelTitleP = document.createElement('p');
      channelTitleP.className = 'chanel-title icon';
      channelTitleP.innerHTML = AppView.cutLongString(element.channelTitle, 20);
      const publishedAtP = document.createElement('p');
      publishedAtP.className = 'date icon';
      const date = new Date(Date.parse(element.publishedAt));
      publishedAtP.innerHTML = `<time datetime=${element.publishedAt}>${date
        .getFullYear()}-${+date
        .getMonth() + 1}-${+date
        .getDate()}</time>`;
      const viewCountP = document.createElement('p');

      viewCountP.className = 'view-count icon';
      viewCountP.innerHTML = element.viewCount;
      videoCard.appendChild(titleDiv);
      videoCard.firstElementChild.style.backgroundImage = `url(${element.imageURL})`;
      videoCard.appendChild(channelTitleP);
      videoCard.appendChild(publishedAtP);
      videoCard.appendChild(viewCountP);
      videoCard.appendChild(descriptionP);
      document.querySelector('.slider').appendChild(videoCard);
    });
  }
}
