import AppNext from './AppNext';

export default class AppSlider {
  constructor(data) {
    this.data = data;
  }

  static getMaxPage(dataLength) {
    const responceClipAmount = AppSlider.getResponceClipAmount();
    const maxPage = Math.ceil(dataLength / responceClipAmount);
    return maxPage - 1;
  }

  static getResponceClipAmount() {
    let responceClipAmount;
    const bodyWidth = parseInt(document.body.offsetWidth, 10);
    if (bodyWidth > 1024) {
      responceClipAmount = 4;
    } else if (bodyWidth <= 1024 && bodyWidth > 768) {
      responceClipAmount = 3;
    } else if (bodyWidth <= 768 && bodyWidth > 480) {
      responceClipAmount = 2;
    } else responceClipAmount = 1;
    return responceClipAmount;
  }

  static dynamicHideButtons(pageNum, max) {
    if (pageNum === 0) {
      document.querySelector('.prev').style.visibility = 'hidden';
      document.querySelector('.start').style.visibility = 'hidden';
    } if (pageNum === 1) {
      document.querySelector('.prev').style.visibility = 'visible';
    }
    if (pageNum === 2) {
      document.querySelector('.start').style.visibility = 'hidden';
    }
    if (pageNum === 3) {
      document.querySelector('.start').style.visibility = 'visible';
    }
    if (pageNum === max) {
      document.querySelector('.next').style.visibility = 'hidden';
    }
    if (pageNum === max - 1) {
      document.querySelector('.next').style.visibility = 'visible';
    }
  }

  addSliderView() {
    /* ------------------------------SWIPE------------------------------- */

    const slider = document.querySelector('.slider');
    let leftClipIndex = 0;
    let moveDirection;
    let isDown = false;
    let startX;
    let pageNum = 0;
    const { data } = this;
    let newDataLength = data.length;
    let maxPageNum = AppSlider.getMaxPage(newDataLength);
    const searchValue = document.body.firstElementChild.value;
    const finalRequest = searchValue.match(/\S+/gi).join('+');

    slider.onmousedown = (e) => {
      moveDirection = 0;
      if (e.target.tagName !== 'A') {
        startX = e.pageX;
        isDown = true;
      } else;
    };

    slider.onmousemove = (e) => {
      if (!isDown) return;
      e.preventDefault();
      const endX = e.pageX;
      moveDirection = endX - startX;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: ${moveDirection}px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    slider.onmouseup = () => {
      isDown = false;
      if (pageNum === 0 && moveDirection < 0) {
        pageNum = 1;
        leftClipIndex = AppSlider.getResponceClipAmount();
      } else if (pageNum !== 0 && moveDirection < 0 && pageNum < maxPageNum) {
        if (pageNum === (maxPageNum - 2) && newDataLength < 45) {
          newDataLength += 15;
          maxPageNum = AppSlider.getMaxPage(newDataLength);
          const appNext = new AppNext(newDataLength, finalRequest);
          appNext.start();
        }
        if (pageNum < maxPageNum) {
          pageNum += 1;
          leftClipIndex += AppSlider.getResponceClipAmount();
        }
      } else if (pageNum !== 0 && moveDirection > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
      } else;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: 0px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
      if (pageNum === 0 || pageNum === 1 || pageNum === 2 || pageNum === 3
        || pageNum === maxPageNum || pageNum === maxPageNum - 1) {
        AppSlider.dynamicHideButtons(pageNum, maxPageNum);
      }
      document.querySelector('.next').setAttribute('data-tooltip', `${pageNum + 2}`);
      document.querySelector('.prev').setAttribute('data-tooltip', `${pageNum - 2}`);
    };

    slider.ontouchstart = (e) => {
      moveDirection = 0;
      if (e.path[0].tagName !== 'A') {
        startX = e.changedTouches[0].pageX;
        isDown = true;
      } else;
    };

    slider.ontouchmove = (e) => {
      if (!isDown) return;
      e.preventDefault();
      const endX = e.changedTouches[0].pageX;
      moveDirection = endX - startX;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: ${moveDirection}px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
    };

    slider.ontouchend = () => {
      isDown = false;
      if (pageNum === 0 && moveDirection < 0) {
        pageNum = 1;
        leftClipIndex = AppSlider.getResponceClipAmount();
      } else if (pageNum !== 0 && moveDirection < 0) {
        if (pageNum === (maxPageNum - 2) && newDataLength < 45) {
          newDataLength += 15;
          maxPageNum = AppSlider.getMaxPage(newDataLength);
          document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
          const appNext = new AppNext(newDataLength, finalRequest);
          appNext.start();
        }
        if (pageNum < maxPageNum) {
          pageNum += 1;
          leftClipIndex += AppSlider.getResponceClipAmount();
        }
      } else if (pageNum !== 0 && moveDirection > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
      } else;
      document.querySelector('.slider').style = `--clip-amount: ${newDataLength}; --page-num: ${pageNum};  --move-direction: 0px`;
      document.querySelector('.current').innerHTML = pageNum + 1;
      if (pageNum === 0 || pageNum === 1 || pageNum === 2 || pageNum === 3
        || pageNum === maxPageNum || pageNum === maxPageNum - 1) {
        AppSlider.dynamicHideButtons(pageNum, maxPageNum);
      }
    };

    /* -------------------------------BUTTONS------------------------------- */

    const buttonSection = document.createElement('section');
    const toStartButton = document.createElement('button');
    const prevButton = document.createElement('button');
    const currentButton = document.createElement('button');
    const nextButton = document.createElement('button');
    nextButton.setAttribute('data-tooltip', `${pageNum + 2}`);
    prevButton.setAttribute('data-tooltip', `${pageNum + 2}`);
    toStartButton.setAttribute('data-tooltip', '1');
    let showingTooltip;

    const tooltipEmersion = (e) => {
      const { target } = e;
      const tooltip = target.getAttribute('data-tooltip');
      if (!tooltip) return;
      const tooltipElem = document.createElement('div');
      tooltipElem.className = 'tooltip';
      tooltipElem.innerHTML = tooltip;
      document.body.appendChild(tooltipElem);
      const coords = target.getBoundingClientRect();
      let left = coords.left + (target.offsetWidth - tooltipElem.offsetWidth) / 2;
      if (left < 0) left = 0;
      let top = coords.top - tooltipElem.offsetHeight - 5;
      if (top < 0) {
        top = coords.top + target.offsetHeight + 5;
      }
      tooltipElem.style.left = `${left}px`;
      tooltipElem.style.top = `${top}px`;
      showingTooltip = tooltipElem;
    };

    document.onmouseover = (e) => { tooltipEmersion(e); };

    document.onmouseout = () => {
      if (showingTooltip) {
        document.body.removeChild(showingTooltip);
        showingTooltip = null;
      }
    };
    document.onclick = (e) => {
      if (showingTooltip) {
        document.body.removeChild(showingTooltip);
        showingTooltip = null;
      }
      tooltipEmersion(e);
      setTimeout(() => {
        if (document.querySelector('.tooltip')) {
          document.querySelector('.tooltip').style.visibility = 'hidden';
        }
      }, 100);
      if (pageNum === 0 && document.querySelector('.tooltip')) {
        document.querySelector('.tooltip').style.visibility = 'hidden';
      }
    };

    buttonSection.className = 'nav';
    toStartButton.className = 'start button';
    toStartButton.innerHTML = '<i class="fas fa-angle-double-left"></i>';
    toStartButton.style.visibility = 'hidden';
    prevButton.className = 'prev button';
    prevButton.style.visibility = 'hidden';
    prevButton.innerHTML = '<i class="fas fa-angle-left"></i>';
    currentButton.className = 'current button';
    currentButton.innerHTML = pageNum + 1;
    nextButton.className = 'next button';
    nextButton.innerHTML = '<i class="fas fa-angle-right"></i>';

    toStartButton.onclick = () => {
      pageNum = 0;
      document.querySelector('.slider').style = `--page-num: 0; --clip-amount: ${newDataLength};`;
      document.querySelector('.current').innerHTML = 1;
      leftClipIndex = 0;
      AppSlider.dynamicHideButtons(pageNum);
      document.querySelector('.prev').style.visibility = 'hidden';
      document.querySelector('.start').style.visibility = 'hidden';
      document.querySelector('.next').style.visibility = 'visible';
    };

    prevButton.onclick = () => {
      if (pageNum > 0) {
        pageNum -= 1;
        leftClipIndex -= AppSlider.getResponceClipAmount();
        document.querySelector('.slider').style = `--page-num: ${pageNum}; --clip-amount: ${newDataLength};`;
        document.querySelector('.current').innerHTML = pageNum + 1;
        if (pageNum === 0 || pageNum === 1 || pageNum === 2 || pageNum === 3
          || pageNum === maxPageNum || pageNum === maxPageNum - 1) {
          AppSlider.dynamicHideButtons(pageNum, maxPageNum);
        }
        document.querySelector('.next').setAttribute('data-tooltip', `${pageNum + 2}`);
        document.querySelector('.prev').setAttribute('data-tooltip', `${pageNum}`);
      }
    };

    nextButton.onclick = () => {
      if (pageNum === (maxPageNum - 2) && newDataLength < 45) {
        newDataLength += 15;
        maxPageNum = AppSlider.getMaxPage(newDataLength);
        document.querySelector('.slider').style = `--clip-amount: ${newDataLength};`;
        const appNext = new AppNext(newDataLength, finalRequest);
        appNext.start();
      }
      if (pageNum < maxPageNum) {
        pageNum += 1;
        leftClipIndex += AppSlider.getResponceClipAmount();
        document.querySelector('.slider').style = `--page-num: ${pageNum}; --clip-amount: ${newDataLength}`;
        document.querySelector('.current').innerHTML = pageNum + 1;
        if (pageNum === 0 || pageNum === 1 || pageNum === 2 || pageNum === 3
          || pageNum === maxPageNum || pageNum === maxPageNum - 1) {
          AppSlider.dynamicHideButtons(pageNum, maxPageNum);
        }
        document.querySelector('.next').setAttribute('data-tooltip', `${pageNum + 2}`);
        document.querySelector('.prev').setAttribute('data-tooltip', `${pageNum}`);
      }
    };

    buttonSection.appendChild(toStartButton);
    buttonSection.appendChild(prevButton);
    buttonSection.appendChild(currentButton);
    buttonSection.appendChild(nextButton);
    document.body.appendChild(buttonSection);

    /* -----------------------------REASSIGN PAGE ON RESIZE-------------------------- */

    const liner = document.createElement('div');
    liner.className = 'liner';
    document.body.appendChild(liner);
    let exClipsPerPage = AppSlider.getResponceClipAmount();
    let clipsPerPage;

    function getLeftClipIndex() {
      leftClipIndex = clipsPerPage * pageNum;
      return leftClipIndex;
    }

    function reassignPage(clipsPPage) {
      pageNum = Math.floor(leftClipIndex / clipsPPage);
      document.querySelector('.slider').style = `--page-num: ${pageNum}; --clip-amount: ${newDataLength};`;
      document.querySelector('.current').innerHTML = pageNum + 1;
      leftClipIndex = getLeftClipIndex();
      exClipsPerPage = clipsPerPage;
      document.querySelector('.next').setAttribute('data-tooltip', `${pageNum + 2}`);
      document.querySelector('.prev').setAttribute('data-tooltip', `${pageNum}`);
    }

    window.onresize = () => {
      if ((getComputedStyle(document.querySelector('.liner')).width) === '1025px') {
        clipsPerPage = 4;
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '1024px') {
        clipsPerPage = 3;
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '768px') {
        clipsPerPage = 2;
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
        }
      } else if ((getComputedStyle(document.querySelector('.liner')).width) === '480px') {
        clipsPerPage = 1;
        if (exClipsPerPage !== clipsPerPage) {
          reassignPage(clipsPerPage);
        }
      }
    };
  }
}
