export default class AppModel {
  constructor(searchState, requestAmount) {
    this.searchState = searchState;
    this.requestAmount = requestAmount;
  }

  static extractClipArray(data, reviews, requestAmount) {
    const clipArray = Array(requestAmount);
    for (let i = 0; i < clipArray.length; i += 1) {
      if (data.items[i]) {
        clipArray[i] = {
          title: data.items[i].snippet.title,
          imageURL: data.items[i].snippet.thumbnails.medium.url,
          description: data.items[i].snippet.description,
          channelTitle: data.items[i].snippet.channelTitle,
          publishedAt: data.items[i].snippet.publishedAt,
          videoID: `https://www.youtube.com/watch?v=${data.items[i].id.videoId}`,
          viewCount: reviews.items[i].statistics.viewCount,
        };
      }
    }
    return clipArray;
  }

  async getClipData() {
    const { dataURL } = this.searchState;

    const dataResponce = await fetch(dataURL);
    const data = await dataResponce.json();

    const iDs = data.items.map(video => video.id.videoId).join(',');
    const reviewsURL = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyB6y5YbKBf9k483GCQXh6EAfItbq8_s00c&id=${iDs}&part=snippet,statistics`;
    const reviewsResponce = await fetch(reviewsURL);
    const reviews = await reviewsResponce.json();

    return AppModel.extractClipArray(data, reviews, this.requestAmount);
  }
}
